import React, { useEffect, useState } from "react";
import { LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from "recharts";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import { fetchCalendarEvents, fetchTrends, fetchCustomers, fetchSales } from "./api";

function App() {
  const [events, setEvents] = useState([]);
  const [trends, setTrends] = useState([]);
  const [customers, setCustomers] = useState([]);
  const [sales, setSales] = useState([]);

  useEffect(() => {
    fetchCalendarEvents().then(setEvents);
    fetchTrends().then(setTrends);
    fetchCustomers().then(setCustomers);
    fetchSales().then(setSales);
  }, []);

  return (
    <div className="p-6 space-y-6 bg-gray-50 min-h-screen">
      <h1 className="text-2xl font-bold">📊 CRM Trends Dashboard</h1>

      {/* Sales Trend */}
      <div className="bg-white rounded-xl shadow p-4">
        <h2 className="text-lg font-semibold mb-2">Sales Trends</h2>
        <ResponsiveContainer width="100%" height={300}>
          <LineChart data={sales}>
            <CartesianGrid stroke="#eee" />
            <XAxis dataKey="sale_date" />
            <YAxis />
            <Tooltip />
            <Line type="monotone" dataKey="total_amount" stroke="#8884d8" />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/* Top Products */}
      <div className="bg-white rounded-xl shadow p-4">
        <h2 className="text-lg font-semibold mb-2">Top Products</h2>
        <ul>
          {trends.map(p => (
            <li key={p.product_id} className="py-1 flex justify-between">
              <span>{p.product_name}</span>
              <span className="text-gray-600">{p.total_sold} sold</span>
            </li>
          ))}
        </ul>
      </div>

      {/* Customers */}
      <div className="bg-white rounded-xl shadow p-4">
        <h2 className="text-lg font-semibold mb-2">Customers</h2>
        <table className="w-full border">
          <thead className="bg-gray-100">
            <tr>
              <th className="p-2">Name</th>
              <th className="p-2">Segment</th>
              <th className="p-2">Purchases</th>
            </tr>
          </thead>
          <tbody>
            {customers.map(c => (
              <tr key={c.id} className="border-t">
                <td className="p-2">{c.name}</td>
                <td className="p-2">{c.customer_segment}</td>
                <td className="p-2">{c.total_purchases}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Calendar */}
      <div className="bg-white rounded-xl shadow p-4">
        <h2 className="text-lg font-semibold mb-2">Calendar Events</h2>
        <FullCalendar
          plugins={[dayGridPlugin]}
          initialView="dayGridMonth"
          events={events.map(e => ({ title: e.name, date: e.date }))}
        />
      </div>
    </div>
  );
}

export default App;
